* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: 'Poppins', sans-serif; }

html, body {
   height: 100%;
   line-height: 1;
   font-size: 15px; }

@mixin gradient {
   background: linear-gradient(90deg, #00F0FF 0%, #5773FF 46.87%, #FF007A 100%);
   background-clip: text;
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent; }

.button {
   position: relative;
   height: 72px;
   width: 293px;
   border-radius: 50px;
   a {
      display: block;
      font-weight: 700;
      font-size: 27.1328px;
      line-height: 41px;
      padding: 16px 58px 15px 57px;
      border-radius: 50px; } }

.box-gradient {
   background-image: linear-gradient(90deg, #00F0FF 0%, #5773FF 46.87%, #FF007A 100%);
   &__background-color {
      background-color: rgb(15, 11, 48); }
   &__bg-gradient {
      background: linear-gradient(90deg, rgba(0, 240, 255, 0.21) 0%, rgba(255, 0, 123, 0.045) 70%); } }

.container {
   max-width: 1391px;
   margin: 0 auto; }

body {
   background: #0b1030 url(../images/test.jpg) top center/100% no-repeat;
   //background-color: #0F0B30
   color: #fff;
   a {
      &:link {
         text-decoration: none;
         color: #fff; }
      &:visited {
         color: #fff; }
      &:hover {
         @include gradient;
         transition: linear, 0.3s;
         -webkit-transition: linear, 0.3s;
         -o-transition: 0.3s linear;
         -moz-transition: 0.3s linear;
         -ms-transition: 0.3s linear; } } }



