.arrow {
   position: relative;
   height: 33px;
   width: 56px;
   margin: 0 auto;
   margin-top: 126px;
   &::before {
      content: "";
      position: absolute;
      width: 3px;
      height: 33px;
      background-color: #fff;
      transform: rotate(-55deg);
      left: 25%; }
   &::after {
      content: "";
      position: absolute;
      width: 3px;
      height: 33px;
      background-color: #fff;
      transform: rotate(55deg);
      right: 25%; } }

