.round-border {
   position: absolute;
   width: 99.3%;
   height: 99%;
   background: #0F0B30;
   border-radius: 50px;
   left: 50%;
   top: 50%;
   transform: translateX(-50%) translateY(-50%);
   overflow: hidden;
   video {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      object-fit: cover; } }

